@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Comfortaa);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

#root, .App {
  width: 100%;
  height: 100%;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
.App {
  background: whitesmoke url(../../static/media/logo-home.14b708fc.png) no-repeat left 5vmin top 5vmin/20%;
}

.signature {
  font-family: 'Comfortaa', sans-serif;
  position: absolute;
  bottom: 1vmin;
  right: 5vmin;
  font-size: 0.8em;
  padding-left: 2em;
  background: url(../../static/media/mugua-dark.64ec1cd8.png) no-repeat center left;
  background-size: 1.5em;
  height: 3%;
  display: flex;
  align-items: center;
  color: #FF8C00;
}

.info {
  position: absolute;
  right: 1vmin;
  top: 1vmin;
  width: 3vmin;
  height: 3vmin;
  background: url(../../static/media/info.6110df0a.png) no-repeat center/contain;
  cursor: pointer;
}
.modal {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(192, 192, 192, 0.95);
  position: absolute;
  padding: 3em;
}

.modal .modal-title {
  padding-bottom: 1em;
  text-transform: capitalize;
}

.loading .modal {
  cursor: progress;
}

.modal .x {
  cursor: pointer;
  float: right;
  width: 3em;
  height: 3em;
  background: transparent url(data:image/png;base64,) no-repeat center center/contain;
}
.collapsible {
  --background-color: rgba(163, 163, 163, 0.9);
  position: absolute;
  height: 100%;
  width: 10vw;
  transition: transform 0.3s;
  z-index: 1;
}

.collapsible .menu {
  height: 100%;
  width: 80%;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-top-right-radius: 1em;
}

.collapsible .menu > p {
  color: whitesmoke;
  font-weight: bold;;
  padding: 1em 0.3em;
  cursor: pointer;
  transition: transform 0.2s;
}

.collapsible .menu > p:hover {
  color: wheat;
  transform: scale(1.2);
}

.collapsible .tab {
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  right: 0;
  height: 10%;
  width: 20%;
  background: var(--background-color) url(data:image/png;base64,) no-repeat center center/60%;
}

.collapsible .tab:hover {
  background-image: url(data:image/png;base64,);
  background-size: 65%;
}

.collapsible.collapsed .tab {
  background-image: url(../../static/media/expand.12b01657.png);
}

.collapsible.collapsed .tab:hover {
  background-image: url(../../static/media/highlighted-expand.ea5bd900.png);
}

.collapsible.collapsed {
  transform: translateX(-8vw);
}
.tags {
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.tag {
  font-size: 0.8em;
  margin: 0.2em;
  border-radius: 0.3em;
  color: whitesmoke;
  background-color: var(--background-color);
  padding: 0.3em;
}
.posts-container {
  --background-color: rgb(77, 77, 179);
  height: 60%;
  width: 25%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 3em;
  display: flex;
  flex-direction: column;
}

.posts-container .caption {
  width: 100%;
  height: 2em;
  background-color: var(--background-color);
  border-radius: 0.5em 0.5em 0 0;
  color: whitesmoke;
  font-weight: bold;
  line-height: 2em;
  padding-left: 1em;
  flex-grow: 0;
  position: relative;
  transition: width 0.5s, border-radius 0.2s;
}

.posts-container .caption.collapsed {
  width: 50%;
  border-radius: 0.5em;
}

.posts-container .feed {
  height: 0;
  flex-grow: 1;
  overflow-y: auto;
  transition: flex-grow 0.2s;
}

.post {
  margin-top: 1em;
  font-size: 0.8em;
  padding: 0 1em 0.5em 0.5em;
  border-bottom: 1px solid var(--background-color);
  border-left: 2px solid var(--background-color);
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
}

.post h2 {
  color: var(--background-color);
  margin-bottom: 0.2em;
}

.post .fullscreen {
  float: right;
  height: 1em;
  width: 1em;
  background: url(data:image/png;base64,) no-repeat center/contain;
  cursor: pointer;
}

.post .content {
  text-align: justify;
}

.post .timestamp {
  padding: 0.5em 0;
  font-size: 0.8em;
  width: 100%;
  text-align: right;
  color: darkslategrey;
}

.feed.collapsed {
  flex-grow: 0;
}

.toggle {
  cursor: pointer;
  height: 1em;
  width: 1em;
  background: url(data:image/png;base64,) no-repeat center/contain;
  position: absolute;
  top: 50%;
  right: 1em;
  transform: translateY(-50%);
}

.toggle:hover {
  background-image: url(data:image/png;base64,);
}

.toggle.expand {
  background-image: url(data:image/png;base64,);
}

.toggle.expand:hover {
  background-image: url(data:image/png;base64,);
}

.wp-block-image {
  width: 25%;
  height: auto;
  float: left;
  margin: 0 1em 1em 0;
}

.wp-block-image img {
  height: 100%;
  width: 100%;
}

.wp-block-image figcaption {
  font-size: 0.8em;
}

.contents {
  height: 95%;
  overflow-y: auto;
}

.contents p {
  text-align: justify;
  margin-bottom: 0.5em;
  margin-right: 1em;
}

.contents p.has-text-align-center, .contents p.has-text-align-right {
  margin-left: 25%;
  margin-right: 25%;
}

.contents p.has-text-align-center {
  text-align: center;
}

.contents p.has-text-align-right {
  text-align: right;
}

.contents p img {
  display: block;
}
.shop {
  --background-color: rgb(77, 77, 179);
  height: 90%;
  width: 65%;
  position: absolute;
  top: 50%;
  right: 3em;
  transform: translateY(-50%);
  display: flex;
  overflow: hidden;
}

.shop .showcase {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  transition: transform 0.3s;
}

.shop .showcase.slided {
  transform: translateX(-100%);
}

.heading {
  width: 100%;
  padding: 1em;
  position: relative;
  background-color: var(--background-color);
  color: whitesmoke;
}

.first .heading {
  border-top-left-radius: 1em;
}

.last .heading {
  border-top-right-radius: 1em;
}

.last .heading > h1 {
  padding-left: 1.5em;
}

.slider {
  cursor: pointer;
  height: 2em;
  width: 2em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider.prev {
  left: 1em;
  background: url(../../static/media/prev.63df6b42.png) no-repeat center/80%;
}

.slider.prev:hover {
  background-image: url(../../static/media/highlighted-prev.2f387292.png);
  background-size: 85%;
}

.slider.next {
  right: 1em;
  background: url(../../static/media/next.04b45844.png) no-repeat center/80%;
}

.slider.next:hover {
  background-image: url(../../static/media/highlighted-next.960c7d17.png);
  background-size: 85%;
}

.selectors {
  width: 100%;
  padding: 0.5em;
  background-color: #b8b8e0;
  display: flex;
  grid-column-gap: 0.5em;
  -webkit-column-gap: 0.5em;
          column-gap: 0.5em;
  font-size: 0.8em;
  line-height: 0.8em;
}

.selectors .selector {
  cursor: pointer;
  height: 2em;
  padding: 0.5em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 0.5em;
  color: #d9d9d9;
  background-color: rgb(163, 163, 163);
}

.selectors .selector.on {
  background-color: var(--background-color);
  color: wheat;
}

.selectors .clear {
  cursor: pointer;
  height: 2em;
  width: 2em;
  background: url(data:image/png;base64,) no-repeat center/contain;
  margin-left: auto;
}

.shop .content {
  padding: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 90%;
  background-color: #dbdbf0;
  overflow-y: auto;
}

.shop .content > .product {
  width: 23%;
  height: 46%;
  overflow: hidden;
  margin: 0.2em;
  position: relative;
  perspective: 1000px;
}

.shop .content > .product.hidden {
  display: none;
}

.shop .content > .product > .product-inner {
  position: relative;
  border-radius: 0.4em;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.shop .content > .product:hover .product-inner{
  transform: rotateY(180deg);
}

.shop .content > .product .front img {
  border-radius: 0.4em;
  width: 100%;
  height: auto;
}

.shop .content > .product .front, .shop .content > .product .back {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background-color: whitesmoke;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.shop .content > .product .back {
  transform: rotateY(180deg);
  font-size: 0.7em;
  padding: 0.5em;
  text-align: justify;
}

.shop .content > .product .back > div[dir="auto"] {
  display: none;
}

.shop .content > .product h6 > span {
  display: none;
}
