.posts-container {
  --background-color: rgb(77, 77, 179);
  height: 60%;
  width: 25%;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 3em;
  display: flex;
  flex-direction: column;
}

.posts-container .caption {
  width: 100%;
  height: 2em;
  background-color: var(--background-color);
  border-radius: 0.5em 0.5em 0 0;
  color: whitesmoke;
  font-weight: bold;
  line-height: 2em;
  padding-left: 1em;
  flex-grow: 0;
  position: relative;
  transition: width 0.5s, border-radius 0.2s;
}

.posts-container .caption.collapsed {
  width: 50%;
  border-radius: 0.5em;
}

.posts-container .feed {
  height: 0;
  flex-grow: 1;
  overflow-y: auto;
  transition: flex-grow 0.2s;
}

.post {
  margin-top: 1em;
  font-size: 0.8em;
  padding: 0 1em 0.5em 0.5em;
  border-bottom: 1px solid var(--background-color);
  border-left: 2px solid var(--background-color);
  border-bottom-left-radius: 0.5em;
  border-bottom-right-radius: 0.5em;
}

.post h2 {
  color: var(--background-color);
  margin-bottom: 0.2em;
}

.post .fullscreen {
  float: right;
  height: 1em;
  width: 1em;
  background: url(./assets/expand.png) no-repeat center/contain;
  cursor: pointer;
}

.post .content {
  text-align: justify;
}

.post .timestamp {
  padding: 0.5em 0;
  font-size: 0.8em;
  width: 100%;
  text-align: right;
  color: darkslategrey;
}

.feed.collapsed {
  flex-grow: 0;
}

.toggle {
  cursor: pointer;
  height: 1em;
  width: 1em;
  background: url(./assets/less.png) no-repeat center/contain;
  position: absolute;
  top: 50%;
  right: 1em;
  transform: translateY(-50%);
}

.toggle:hover {
  background-image: url(./assets/highlighted-less.png);
}

.toggle.expand {
  background-image: url(./assets/more.png);
}

.toggle.expand:hover {
  background-image: url(./assets/highlighted-more.png);
}

.wp-block-image {
  width: 25%;
  height: auto;
  float: left;
  margin: 0 1em 1em 0;
}

.wp-block-image img {
  height: 100%;
  width: 100%;
}

.wp-block-image figcaption {
  font-size: 0.8em;
}

.contents {
  height: 95%;
  overflow-y: auto;
}

.contents p {
  text-align: justify;
  margin-bottom: 0.5em;
  margin-right: 1em;
}

.contents p.has-text-align-center, .contents p.has-text-align-right {
  margin-left: 25%;
  margin-right: 25%;
}

.contents p.has-text-align-center {
  text-align: center;
}

.contents p.has-text-align-right {
  text-align: right;
}

.contents p img {
  display: block;
}