.shop {
  --background-color: rgb(77, 77, 179);
  height: 90%;
  width: 65%;
  position: absolute;
  top: 50%;
  right: 3em;
  transform: translateY(-50%);
  display: flex;
  overflow: hidden;
}

.shop .showcase {
  width: 100%;
  height: 100%;
  flex-shrink: 0;
  transition: transform 0.3s;
}

.shop .showcase.slided {
  transform: translateX(-100%);
}

.heading {
  width: 100%;
  padding: 1em;
  position: relative;
  background-color: var(--background-color);
  color: whitesmoke;
}

.first .heading {
  border-top-left-radius: 1em;
}

.last .heading {
  border-top-right-radius: 1em;
}

.last .heading > h1 {
  padding-left: 1.5em;
}

.slider {
  cursor: pointer;
  height: 2em;
  width: 2em;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.slider.prev {
  left: 1em;
  background: url(./assets/prev.png) no-repeat center/80%;
}

.slider.prev:hover {
  background-image: url(./assets/highlighted-prev.png);
  background-size: 85%;
}

.slider.next {
  right: 1em;
  background: url(./assets/next.png) no-repeat center/80%;
}

.slider.next:hover {
  background-image: url(./assets/highlighted-next.png);
  background-size: 85%;
}

.selectors {
  width: 100%;
  padding: 0.5em;
  background-color: #b8b8e0;
  display: flex;
  column-gap: 0.5em;
  font-size: 0.8em;
  line-height: 0.8em;
}

.selectors .selector {
  cursor: pointer;
  height: 2em;
  padding: 0.5em;
  width: fit-content;
  border-radius: 0.5em;
  color: #d9d9d9;
  background-color: rgb(163, 163, 163);
}

.selectors .selector.on {
  background-color: var(--background-color);
  color: wheat;
}

.selectors .clear {
  cursor: pointer;
  height: 2em;
  width: 2em;
  background: url(./assets/clear.png) no-repeat center/contain;
  margin-left: auto;
}

.shop .content {
  padding: 1em;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  height: 90%;
  background-color: #dbdbf0;
  overflow-y: auto;
}

.shop .content > .product {
  width: 23%;
  height: 46%;
  overflow: hidden;
  margin: 0.2em;
  position: relative;
  perspective: 1000px;
}

.shop .content > .product.hidden {
  display: none;
}

.shop .content > .product > .product-inner {
  position: relative;
  border-radius: 0.4em;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.shop .content > .product:hover .product-inner{
  transform: rotateY(180deg);
}

.shop .content > .product .front img {
  border-radius: 0.4em;
  width: 100%;
  height: auto;
}

.shop .content > .product .front, .shop .content > .product .back {
  width: 100%;
  height: 100%;
  border-radius: inherit;
  background-color: whitesmoke;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

.shop .content > .product .back {
  transform: rotateY(180deg);
  font-size: 0.7em;
  padding: 0.5em;
  text-align: justify;
}

.shop .content > .product .back > div[dir="auto"] {
  display: none;
}

.shop .content > .product h6 > span {
  display: none;
}