@import url(https://fonts.googleapis.com/css?family=Comfortaa);

.App {
  background: whitesmoke url(./assets/logo-home.png) no-repeat left 5vmin top 5vmin/20%;
}

.signature {
  font-family: 'Comfortaa', sans-serif;
  position: absolute;
  bottom: 1vmin;
  right: 5vmin;
  font-size: 0.8em;
  padding-left: 2em;
  background: url(./assets/mugua-dark.png) no-repeat center left;
  background-size: 1.5em;
  height: 3%;
  display: flex;
  align-items: center;
  color: #FF8C00;
}

.info {
  position: absolute;
  right: 1vmin;
  top: 1vmin;
  width: 3vmin;
  height: 3vmin;
  background: url(./assets/info.png) no-repeat center/contain;
  cursor: pointer;
}