.collapsible {
  --background-color: rgba(163, 163, 163, 0.9);
  position: absolute;
  height: 100%;
  width: 10vw;
  transition: transform 0.3s;
  z-index: 1;
}

.collapsible .menu {
  height: 100%;
  width: 80%;
  background-color: var(--background-color);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  border-top-right-radius: 1em;
}

.collapsible .menu > p {
  color: whitesmoke;
  font-weight: bold;;
  padding: 1em 0.3em;
  cursor: pointer;
  transition: transform 0.2s;
}

.collapsible .menu > p:hover {
  color: wheat;
  transform: scale(1.2);
}

.collapsible .tab {
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  right: 0;
  height: 10%;
  width: 20%;
  background: var(--background-color) url(./assets/collapse.png) no-repeat center center/60%;
}

.collapsible .tab:hover {
  background-image: url(./assets/highlighted-collapse.png);
  background-size: 65%;
}

.collapsible.collapsed .tab {
  background-image: url(./assets/expand.png);
}

.collapsible.collapsed .tab:hover {
  background-image: url(./assets/highlighted-expand.png);
}

.collapsible.collapsed {
  transform: translateX(-8vw);
}