.modal {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(192, 192, 192, 0.95);
  position: absolute;
  padding: 3em;
}

.modal .modal-title {
  padding-bottom: 1em;
  text-transform: capitalize;
}

.loading .modal {
  cursor: progress;
}

.modal .x {
  cursor: pointer;
  float: right;
  width: 3em;
  height: 3em;
  background: transparent url(./assets/close.png) no-repeat center center/contain;
}